body {
  margin: 0;
  margin-bottom: 30mm;
}
h1,.h1 {
  font-size: 20pt;
}
.print_page {
  width: 58mm;
  height: auto;
  margin-bottom: 30mm;
  page-break-after: always;
  font-size: 10pt;
}
.print_page:last-child {
  page-break-after: auto; /* æå¾ã®ãã¼ã¸ã®æ¹ãã¼ã¸ãé²ã */
}
ul {
  list-style: none;
  padding: 0;
}
@page {
  size: 58mm auto;
  margin: 0;
  margin-bottom: 30mm;
}
@media print {
  body {
    width: 58mm; /* needed for Chrome */
  }
}
@media screen {
  body {
    background: #eee;
  }
  .print_page {
    background: white; /* èæ¯ãç½ã */
    box-shadow: 0 .5mm 2mm rgba(0,0,0,.3); /* ãã­ããã·ã£ãã¦ */
    margin: 0mm;
  }
}
.flex-box {
